const strokeWeight = window.selectBoxStyle.strokeWeight || 3;
const strokeColor = window.selectBoxStyle.strokeColor || "#67C23A";
const fillColor = window.selectBoxStyle.fillColor || "rgb(230, 162, 60, 0.5)";

class CesiumDrawGraphics {
  doubleClickTimeout = null;
  viewer = null;
  handler = null;
  entityCollection = null;
  drawingMode = "polygon";

  activeShape = undefined;
  floatingPoint = undefined;
  activeShapePoints = [];

  callback = null;

  pointOptions = {
    color: Cesium.Color.fromCssColorString("#E6A23C").withAlpha(0),
    pixelSize: 8,
    outlineColor: Cesium.Color.fromCssColorString("#000").withAlpha(0),
    outlineWidth: 2,
    heightReference: Cesium.HeightReference.CLAMP_TO_GROUND,
  };

  polylineOptions = {
    clampToGround: true,
    width: strokeWeight,
    material: Cesium.Color.fromCssColorString(strokeColor),
  };

  polygonOptions = {
    material: new Cesium.ColorMaterialProperty(
      Cesium.Color.fromCssColorString(fillColor)
    ),
  };

  constructor(options) {
    this.viewer = options.viewer;
    this.callback = options.callback;
    this.entityCollection = new Cesium.CustomDataSource("geojson-draw-layers");
    this.viewer.dataSources.add(this.entityCollection);
  }
  create() {
    if (this.handler) return;
    this.handler = new Cesium.ScreenSpaceEventHandler(this.viewer.canvas);
    this.handler.setInputAction(
      this.mouseLeft.bind(this),
      Cesium.ScreenSpaceEventType.LEFT_CLICK
    );
    this.handler.setInputAction(
      this.mouseMove.bind(this),
      Cesium.ScreenSpaceEventType.MOUSE_MOVE
    );
    this.handler.setInputAction(
      this.mouseDoubleClcik.bind(this),
      Cesium.ScreenSpaceEventType.LEFT_DOUBLE_CLICK
    );
    this.handler.setInputAction(
      this.mouseRight.bind(this),
      Cesium.ScreenSpaceEventType.RIGHT_CLICK
    );
    this.viewer.canvas.style.cursor = "crosshair";
  }
  destroy() {
    if (this.handler) {
      this.handler.removeInputAction(
        this.mouseLeft.bind(this),
        Cesium.ScreenSpaceEventType.LEFT_CLICK
      );
      this.handler.removeInputAction(
        this.mouseMove.bind(this),
        Cesium.ScreenSpaceEventType.MOUSE_MOVE
      );
      this.handler.removeInputAction(
        this.mouseDoubleClcik.bind(this),
        Cesium.ScreenSpaceEventType.LEFT_DOUBLE_CLICK
      );
      this.handler.removeInputAction(
        this.mouseRight.bind(this),
        Cesium.ScreenSpaceEventType.RIGHT_CLICK
      );
      this.handler.destroy();
      this.handler = null;
    }
    this.viewer.canvas.style.cursor = "default";
  }
  mouseLeft(event) {
    const ray = this.viewer.camera.getPickRay(event.position);
    const earthPosition = this.viewer.scene.globe.pick(ray, this.viewer.scene);
    if (Cesium.defined(earthPosition)) {
      if (this.activeShapePoints.length === 0) {
        this.floatingPoint = this.createPoint(earthPosition);
        this.activeShapePoints.push(earthPosition);
        const dynamicPolygonPositions = new Cesium.CallbackProperty(() => {
          return new Cesium.PolygonHierarchy(this.activeShapePoints);
        }, false);
        const dynamicPolylinePositions = new Cesium.CallbackProperty(() => {
          return this.activeShapePoints;
        }, false);

        this.activeShape = this.drawShape(dynamicPolygonPositions, dynamicPolylinePositions);
      }
      this.activeShapePoints.push(earthPosition);
      this.createPoint(earthPosition);
      
    }
  }
  mouseMove(event) {
    if (Cesium.defined(this.floatingPoint)) {
      const ray = this.viewer.camera.getPickRay(event.endPosition);
      const newPosition = this.viewer.scene.globe.pick(ray, this.viewer.scene);
      if (Cesium.defined(newPosition)) {
        this.floatingPoint.position.setValue(newPosition);
        this.activeShapePoints.pop();
        this.activeShapePoints.push(newPosition);
      }
    }
  }
  mouseRight(event) {
    if (this.activeShapePoints.length > 2) {
      this.activeShapePoints.splice(-2, 1);
    }else {
      this.activeShapePoints = [];
    }
  }
  mouseDoubleClcik(event) {
    this.activeShapePoints.splice(-3);
    // const ray = this.viewer.camera.getPickRay(event.position);
    // const newPosition = this.viewer.scene.globe.pick(ray, this.viewer.scene);
      if (this.activeShapePoints.length < 3) {
        this.activeShapePoints = [];
      }else {
        const points = this.activeShapePoints;
        points.push(points[0]);
        if(typeof this.callback === "function") {
          this.callback(this.activeShapePoints);
        }        
        this.terminateShape();        
      }  
  }
  createPoint(worldPosition) {
    const point = this.entityCollection.entities.add({
      position: worldPosition,
      point: this.pointOptions,
    });
    return point;
  }
  drawShape(polygonPosition, dynamicPolylinePositions) {
    let shape = this.entityCollection.entities.add({
      polyline: {
        positions: dynamicPolylinePositions,
        ...this.polylineOptions,
      },
      polygon: { hierarchy: polygonPosition, ...this.polygonOptions },
    });
    return shape;
  }
  terminateShape() {
    this.drawShape(this.activeShapePoints, this.activeShapePoints);
    this.entityCollection.entities.remove(this.floatingPoint);
    this.entityCollection.entities.remove(this.activeShape);
    this.floatingPoint = undefined;
    this.activeShape = undefined;
    this.activeShapePoints = [];
  }
  clear() {
    this.entityCollection.entities.removeAll();
  }
}

export default CesiumDrawGraphics;
